          nextRowIndex = !bottom ? (!len ? cellInfo.rowIndex - 1 : range.beginRowIndex - 1)
            : (!len ? (cellInfo.rowIndex + cellInfo.rowSpan) : range.endRowIndex + 1)
          nextColIndex = !len ? cellInfo.colIndex : range.beginColIndex
